import _ from "lodash";
import { Button, Dropdown, Typography } from "antd";
import styled from "styled-components";
import { useTranslation, withTranslation } from "react-i18next";
import { memo, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import ModalChangeComponent from "./ModalChangeComponent";
import {
  getTemplateDefaultCallView,
  updateTemplateCallView,
} from "redux/slices/callView";
import IconSettings from "assets/icons/callView/IconSettings.svg";
// import IconEditData from "assets/icons/callView/IconEditData.svg";
import IconChangeComponent from "assets/icons/callView/IconChangeComponent.svg";
import IconRemove from "assets/icons/callView/iconRemove.svg";
import IconEditTemplate from "assets/icons/callView/IconEditTemplate.svg";
import { useLocation, useNavigate } from "react-router";
import ModalUpdateTabs from "./ModalUpdateTabs";
import {
  componentsCallViewSettings,
  handlerIconComp,
  isValid_Id,
} from "util/staticData";
import Icon from "components/Icon/Icon";
//test
// import FormFieldObject from "components/FormFieldObject/FormFieldObject";

const { Text } = Typography;

const LayoutCallView1 = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { search } = useLocation();
  const queryParams = new URLSearchParams(search);
  const templateId = queryParams.get("template");
  const [visible, setVisible] = useState(false);
  const [visibleTab, setVisibleTab] = useState(false);
  const [componentList, setComponentlist] = useState([]);
  const { templateDefault } = useSelector((state) => state.callViewReducer);
  const [itemSelected, setItemSelected] = useState(null);
  const [keySelected, setKeySelected] = useState(null);
  const [keyTabActive, setKeyTabActive] = useState([0, 0, 0, 0]);
  const [keyNewTab, setKeyNewTab] = useState(false);
  const [listTab, setListTab] = useState(null);
  const [data, setData] = useState(componentsCallViewSettings);
  const [componentsCreated, $componentsCreated] = useState([]);
  const { components } = useSelector(
    (state) => state.consolidatedViewSettingsReducer
  );

  const handleOpenModalAdd = (addNew) => {
    setVisible(true);
    setItemSelected(addNew ? null : _.get(componentList, `[${keySelected}]`));
  };

  const normalizeComponents = (items) => {
    return _.map(
      items,
      (item) =>
        (item = {
          ...item,
          title: item.name,
          icon: handlerIconComp(
            _.get(item, "type") || _.get(item, "component_type")
          ),
        })
    );
  };

  const handleRemove = () => {
    let newList = [...componentList];
    newList.splice(Number(keySelected), 1, {});

    dispatch(
      updateTemplateCallView({
        object_id: "obj_contact_00000001",
        template: templateId ? templateId.toString() : "",
        components: newList,
      })
    );
  };

  const items = [
    // {
    //   key: "1",
    //   label: (
    //     <Option>
    //       <IconCustomPopup src={IconEditData} alt="" />
    //       <div>{t("CallView.editData")}</div>
    //     </Option>
    //   ),
    // },
    {
      key: "2",
      label: (
        <Option
          onClick={() => {
            handleOpenModalAdd(false);
          }}
        >
          <IconCustomPopup src={IconChangeComponent} alt="" />
          <div>{t("CallView.changeComponent")}</div>
        </Option>
      ),
    },
    {
      key: "3",
      label: (
        <Option
          onClick={() => {
            handleRemove();
          }}
        >
          <IconCustomPopup src={IconRemove} alt="" />
          <div>{t("common.remove")}</div>
        </Option>
      ),
    },
  ];

  const renderComponent = (component, index) => {
    const isTab = _.get(component, `type`) === "tab";
    const componentTab = isTab
      ? _.get(
          component,
          `tab_component.${_.get(
            component,
            `tab_name[${keyTabActive[index]}]`
          )}`
        )
      : null;
    const title = isTab
      ? _.get(componentTab, "[0].i")
      : _.get(component, "title");
    const des = _.get(component, "des");
    const objectName = _.get(component, "object_name");
    const icon = isTab
      ? _.get(
          data.find(
            (item) =>
              _.get(item, "_id") === title || _.get(item, "title") === title
          ),
          "icon"
        )
      : _.get(component, "icon");

    return (
      <ItemWrapper>
        {title && <ImgCustom src={icon} alt="" />}

        <div className="item-content">
          <p>
            {isValid_Id(title)
              ? `${_.get(
                  data.find((item) => _.get(item, "_id") === title),
                  "title"
                )}`
              : title || "Please select component"}
          </p>
          {!isTab && (
            <div
              style={{ textTransform: "capitalize" }}
              ellipsis={{ tooltip: des }}
            >
              {des
                ? des.toLowerCase().replaceAll("_", " ")
                : `Object: ${objectName}`}
            </div>
          )}
        </div>
      </ItemWrapper>
    );
  };
  const RenderItemByIndex = (index) => {
    const listTabItem = _.get(componentList, `[${index}].tab_name`, []);
    return _.get(componentList, `[${index}].title`) ? (
      <>
        <HeaderLayout>
          {_.get(componentList, `[${index}].type`) === "tab" ? (
            <WrapperTabs>
              {listTabItem &&
                listTabItem.map((tab, indexTab) => {
                  const listComponent = _.get(
                    componentList,
                    `[${index}].tab_component.${tab}`
                  );
                  return (
                    <Tabs
                      className={
                        keyTabActive[index] === indexTab ? "active" : ""
                      }
                      onClick={() => {
                        setKeyTabActive((pre) => {
                          pre.splice(index, 1, indexTab);
                          let newArr = [...pre];
                          return newArr;
                        });
                        setKeySelected(index);
                      }}
                      onDoubleClick={() => {
                        setKeyNewTab(false);
                        setKeySelected(index);
                        setVisibleTab(true);
                        setListTab(listTabItem);
                      }}
                    >
                      <Icon
                        icon={_.get(listComponent, "[0].icon")}
                        size={20}
                        color={
                          keyTabActive[index] === indexTab ? "#000" : "#6B6B6B"
                        }
                      />
                      <TextCustom
                        className={
                          keyTabActive[index] === indexTab ? "active" : ""
                        }
                        ellipsis={{ tooltip: tab }}
                        style={{ maxWidth: 100 }}
                      >
                        {tab}
                      </TextCustom>
                    </Tabs>
                  );
                })}
              <div className="iconAdd">
                <Icon
                  icon="plus"
                  size={20}
                  color="#637381"
                  style={{ cursor: "pointer", minWidth: "16px" }}
                  onClick={() => {
                    setKeyNewTab(true);
                    setVisibleTab(true);
                    setKeySelected(index);
                    setListTab(listTabItem);
                  }}
                />
              </div>
            </WrapperTabs>
          ) : (
            <div className="title">
              {_.get(componentList, `[${index}].title`)}
            </div>
          )}
          <Dropdown
            trigger={["click"]}
            menu={{ items }}
            placement="bottomRight"
            arrow
            onClick={() => {
              setKeySelected(index);
            }}
          >
            <IconCustom src={IconSettings} alt="" />
          </Dropdown>
        </HeaderLayout>
        <ContentLayout>
          <div className="content">
            {renderComponent(_.get(componentList, `[${index}]`), index)}
          </div>
        </ContentLayout>
      </>
    ) : (
      <CustomButton
        onClick={() => {
          setKeySelected(index);
          handleOpenModalAdd(true);
        }}
        type="primary"
      >
        {t("CallView.addNew")}
      </CustomButton>
    );
  };

  useEffect(() => {
    dispatch(
      getTemplateDefaultCallView({
        object_id: "obj_contact_00000001",
      })
    );
  }, [dispatch]);

  useEffect(() => {
    if (templateDefault) {
      setComponentlist(_.get(templateDefault, "components"));
    }
  }, [templateDefault]);

  useEffect(() => {
    $componentsCreated(normalizeComponents(components));
    //eslint-disable-next-line
  }, [components]);

  useEffect(() => {
    setData((pre) => [...componentsCreated, ...pre]);
  }, [componentsCreated]);

  //test show

  return (
    <Wrapper>
      {/* <FormFieldObject
        titleHeader={"Thng tin interaction"}
        object_id_parent={"obj_contact_00000001"}
        object_id={"obj_interaction_00000001"}
        UrlAPI={"interactions/view-interaction-new"}
        method="POST"
        payload={{ third_party_id: "1002" }}
        //config
        isConfig
        component_id={_.get(componentList, "[3]._id")}
        footer={false}
        maxHeight={374}
      />

      <FormFieldObject
        titleHeader={"Thng tin Contact"}
        object_id={"obj_contact_00000001"}
        UrlAPI={
          "load-record-data?id=651b9df973e73c98ce1cf6d0&object_id=obj_contact_00000001"
        }
        method="GET"
        cols={2}
        footer={true}
        maxHeight={553}
      /> */}
      <div style={{ textAlign: "right" }}>
        <CustomButton
          onClick={() => navigate("/call-view-settings", { replace: true })}
        >
          <img src={IconEditTemplate} alt="" />
          <span>{t("CallView.changeTemplate")}</span>
        </CustomButton>
      </div>
      {componentList && _.size(componentList) > 0 && (
        <>
          <WrapperContent>
            <FirstLayout
              className={_.get(componentList, "[0].title") ? "active" : ""}
            >
              {RenderItemByIndex(0)}
            </FirstLayout>
            <SecondLayout
              className={_.get(componentList, "[1].title") ? "active" : ""}
            >
              {RenderItemByIndex(1)}
            </SecondLayout>
          </WrapperContent>
          <ThirdLayout
            className={_.get(componentList, "[2].title") ? "active" : ""}
          >
            {RenderItemByIndex(2)}
          </ThirdLayout>
          <FourthLayout
            className={_.get(componentList, "[3].title") ? "active" : ""}
          >
            {RenderItemByIndex(3)}
          </FourthLayout>
        </>
      )}
      <ModalChangeComponent
        visible={visible}
        setVisible={setVisible}
        keySelectedProp={keySelected}
        itemSelectedProp={itemSelected}
        componentList={componentList}
      />
      <ModalUpdateTabs
        visible={visibleTab}
        setVisible={setVisibleTab}
        keyTabActive={keyTabActive}
        listTab={listTab}
        componentList={componentList}
        keySelected={keySelected}
        setKeyTabActive={setKeyTabActive}
        templateId={templateId ? templateId.toString() : ""}
        keyNewTab={keyNewTab}
      />
    </Wrapper>
  );
};

export default withTranslation()(memo(LayoutCallView1));

