import { Button, Input, Select, Typography } from "antd";
import Modal from "antd/lib/modal/Modal";
import _ from "lodash";
import React, { memo, useEffect, useRef, useState } from "react";
import { useTranslation, withTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import {
  componentsCallViewSettings,
  handlerIconComp,
  iconTabComp,
  isValid_Id,
} from "util/staticData";
import IconActive from "assets/icons/callView/tick-circle.svg";
import { updateTemplateCallView } from "redux/slices/callView";
import { Notification } from "components/Notification/Noti";

const { Text } = Typography;

const ModalUpdateTabs = ({
  visible,
  setVisible,
  keyNewTab,
  keyTabActive,
  setKeyTabActive,
  listTab,
  componentList,
  keySelected,
  templateId,
}) => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const elementRef = useRef(null);
  const [componentsCreated, $componentsCreated] = useState([]);
  const [itemSelected, setItemSelected] = useState(null);
  const [titleTab, setTitleTab] = useState("");
  const [iconTab, setIconTab] = useState("");
  const [error, setError] = useState(false);
  const [firstSubmit, setFirstSubmit] = useState(false);
  const [data, setData] = useState([]);
  const { components } = useSelector(
    (state) => state.consolidatedViewSettingsReducer
  );

  const handleScrollToElement = () => {
    if (elementRef?.current) {
      setTimeout(() => {
        elementRef.current.scrollTop = 0;
      }, 100);
    }
  };

  const handleClose = () => {
    setVisible(false);
  };

  const handleRemove = () => {
    let updatedComponentList = [...componentList];
    let Tabs = _.get(componentList, `[${keySelected}]`);
    let TabName = [..._.get(Tabs, "tab_name")];
    let oldName = _.get(TabName, `[${keyTabActive[keySelected]}]`);
    let TabComponent = { ..._.get(Tabs, "tab_component") };
    delete TabComponent[oldName];
    TabName.splice(keyTabActive[keySelected], 1);

    updatedComponentList.splice(Number(keySelected), 1, {
      ...Tabs,
      tab_component: TabComponent,
      tab_name: TabName,
    });

    dispatch(
      updateTemplateCallView({
        object_id: "obj_contact_00000001",
        template: templateId ? templateId.toString() : "",
        components: updatedComponentList,
      })
    );

    setKeyTabActive((pre) => {
      pre.splice(keySelected, 1, 0);
      let newArr = [...pre];
      return newArr;
    });
    setVisible(false);
  };

  const handleSave = () => {
    if (!firstSubmit) setFirstSubmit(true);
    if (!error) {
      if (!keyNewTab) {
        if (keySelected || keySelected === 0) {
          let payloadComponentList = [...componentList];
          let Tabs = _.get(componentList, `[${keySelected}]`);
          let TabName = [..._.get(Tabs, "tab_name")];
          let oldName = _.get(TabName, `[${keyTabActive[keySelected]}]`);
          let TabComponent = { ..._.get(Tabs, "tab_component") };
          let oldComponent = _.get(TabComponent, `${oldName}`);

          const listNameTabs = Object.keys(TabComponent)
            ? Object.keys(TabComponent).filter((item) => item !== oldName)
            : [];

          if (!listNameTabs.includes(titleTab)) {
            let newComponent =
              oldComponent && _.size(oldComponent) > 0
                ? oldComponent.map((item) => {
                    return {
                      ...item,
                      icon: iconTab,
                      i:
                        _.get(itemSelected, "_id") ||
                        _.get(itemSelected, "title") ||
                        _.get(item, "i"),
                      component_setting: data.find(
                        (item) =>
                          _.get(item, "_id") === _.get(itemSelected, "_id")
                      ),
                    };
                  })
                : [
                    {
                      h: 12,
                      w: 12,
                      x: 0,
                      y: 0,
                      icon: iconTab,
                      i:
                        _.get(itemSelected, "_id") ||
                        _.get(itemSelected, "title"),
                      component_setting: data.find(
                        (item) =>
                          _.get(item, "_id") === _.get(itemSelected, "_id")
                      ),
                    },
                  ];

            if (oldName !== titleTab) {
              delete TabComponent[oldName];
              TabComponent[titleTab] = newComponent;
            } else {
              TabComponent[oldName] = newComponent;
            }
            TabName.splice(Number(keyTabActive[keySelected]), 1, titleTab);
            payloadComponentList.splice(Number(keySelected), 1, {
              ...Tabs,
              tab_component: TabComponent,
              tab_name: TabName,
            });

            dispatch(
              updateTemplateCallView({
                object_id: "obj_contact_00000001",
                template: templateId ? templateId.toString() : "",
                components: payloadComponentList,
              })
            );

            setVisible(false);
          } else {
            Notification("warning", "Tn tab khng c trng!");
          }
        }
      } else {
        //new tab
        let payloadComponentList = [...componentList];
        let Tabs = _.get(componentList, `[${keySelected}]`);
        let TabName = [..._.get(Tabs, "tab_name")];

        let TabComponent = { ..._.get(Tabs, "tab_component") };

        const listNameTabs = Object.keys(TabComponent);

        console.log(listNameTabs, titleTab);

        if (!listNameTabs.includes(titleTab)) {
          TabName.push(titleTab);
          TabComponent[titleTab] = [
            {
              h: 12,
              w: 12,
              x: 0,
              y: 0,
              icon: iconTab,
              i: _.get(itemSelected, "_id") || _.get(itemSelected, "title"),
              component_setting: data.find(
                (item) => _.get(item, "_id") === _.get(itemSelected, "_id")
              ),
            },
          ];

          payloadComponentList.splice(Number(keySelected), 1, {
            ...Tabs,
            tab_component: TabComponent,
            tab_name: TabName,
          });

          dispatch(
            updateTemplateCallView({
              object_id: "obj_contact_00000001",
              template: templateId ? templateId.toString() : "",
              components: payloadComponentList,
            })
          );

          setKeyTabActive((pre) => {
            pre.splice(keySelected, 1, Number(_.size(listTab)));
            let newArr = [...pre];
            return newArr;
          });
          setVisible(false);
        } else {
          Notification("warning", "Tn tab khng c trng!");
        }
      }
    }
  };

  const normalizeComponents = (items) => {
    return _.map(
      items,
      (item) =>
        (item = {
          ...item,
          title: item.name,
          icon: handlerIconComp(
            _.get(item, "type") || _.get(item, "component_type")
          ),
        })
    );
  };

  const renderComponent = (item) => {
    const title = _.get(item, "title");
    const des = _.get(item, "des");
    const icon = _.get(item, "icon");
    const objectName = _.get(item, "object_name");
    const relatedObjectName = _.get(item, "related_object_name");
    const isActive = _.get(itemSelected, "_id")
      ? _.get(item, "_id") === _.get(itemSelected, "_id")
      : title === _.get(itemSelected, "title");
    return (
      <Component
        className={isActive ? "active" : ""}
        onClick={() => {
          setItemSelected(item);
        }}
      >
        <div
          style={{
            display: "flex",
            alignItems: "center",
          }}
        >
          <img src={icon} alt="" />

          <div>
            <p>{title}</p>
            <Text
              style={{ textTransform: "capitalize" }}
              ellipsis={{ tooltip: des }}
            >
              {des
                ? des.toLowerCase().replaceAll("_", " ")
                : relatedObjectName
                ? `Related object: ${relatedObjectName}`
                : objectName
                ? `object: ${objectName}`
                : ""}
            </Text>
          </div>
        </div>
        {isActive && <img src={IconActive} alt="icon" />}
      </Component>
    );
  };

  useEffect(() => {
    $componentsCreated(normalizeComponents(components));
    //eslint-disable-next-line
  }, [components]);

  useEffect(() => {
    if (componentsCreated && _.size(componentsCreated) > 0) {
      setData(
        [...componentsCreated, ...componentsCallViewSettings].filter(
          (item) => _.get(item, "type") !== "tab"
        )
      );
    }
  }, [componentsCreated]);

  useEffect(() => {
    if (!keyNewTab) {
      let listTabComponent = _.get(
        componentList,
        `[${keySelected}].tab_component.${_.get(
          componentList,
          `[${keySelected}].tab_name[${keyTabActive[keySelected]}]`
        )}`
      );

      setTitleTab(_.get(listTab, `[${keyTabActive[keySelected]}]`));
      setIconTab(_.get(listTabComponent, `[0].icon`));
      setItemSelected(
        isValid_Id(_.get(listTabComponent, `[0].i`))
          ? {
              _id: _.get(listTabComponent, `[0].i`),
            }
          : {
              title: _.get(listTabComponent, `[0].i`),
            }
      );
    } else {
      setItemSelected(null);
      setTitleTab("");
      setIconTab(_.get(iconTabComp, "[0].key"));
    }

    setFirstSubmit(false);
  }, [visible, keyTabActive, listTab, componentList, keySelected, keyNewTab]);

  useEffect(() => {
    if (!titleTab) {
      setError(true);
    } else {
      setError(false);
    }
  }, [titleTab]);

  useEffect(() => {
    if (visible) {
      handleScrollToElement();
    }
  }, [visible]);

  return (
    <CustomModal
      title={"Tab"}
      centered
      open={visible}
      onCancel={handleClose}
      width={651}
      footer={null}
    >
      <CustomContent ref={elementRef}>
        <WrapperTabName className={error && firstSubmit ? "err" : ""}>
          <div className="title">Tab name</div>
          <WrapItem>
            <Select
              style={{ width: "70px" }}
              onChange={(value) => setIconTab(value)}
              value={iconTab}
            >
              {_.map(iconTabComp, (icon) => (
                <Select.Option value={_.get(icon, "key")}>
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    {_.get(icon, "icon")}
                  </div>
                </Select.Option>
              ))}
            </Select>
            <div>
              <Input
                style={{ border: "none" }}
                placeholder={t("common.placeholderInput")}
                value={titleTab}
                onChange={(e) => setTitleTab(e.target.value)}
                onPressEnter={handleSave}
                autoFocus
              />
              {error && firstSubmit && (
                <span className="err">{t("common.placeholderInput")}</span>
              )}
            </div>
          </WrapItem>
        </WrapperTabName>
        {_.map(data, (item, idx) => (
          <div key={idx}>{renderComponent(item)}</div>
        ))}
      </CustomContent>
      <CustomFooter>
        {!keyNewTab && (
          <CustomButtonCancel onClick={handleRemove}>
            {t("common.remove")}
          </CustomButtonCancel>
        )}
        <CustomButtonCancel
          onClick={handleClose}
          style={{ marginLeft: "16px" }}
        >
          {t("CallView.cancel")}
        </CustomButtonCancel>

        <CustomButtonSave onClick={handleSave}>
          {t("CallView.save")}
        </CustomButtonSave>
      </CustomFooter>
    </CustomModal>
  );
};

export default withTranslation()(memo(ModalUpdateTabs));

